<template>
  <div class="welcome-area-container">
    <h2>迎宾区域</h2>
    <div class="welcome-area-wrapper">
      <div class="staff-wrapper">
        <div class="title">
          值班员工
        </div>
        <div class="infos">
          <div
              v-for="(staff, i) in staffs"
              :key="i"
              class="staff"
          >
            <div class="staff-info">
              <img :src="staff.img" alt="">
              <div class="info">
                <div>姓名: {{ staff.name }}</div>
                <div>工号: {{ staff.age }}</div>
              </div>
            </div>
            <div class="time">
              <span>当前时段离岗时间: &nbsp;</span>
              <span>{{ staff.leaveTime }}分钟</span>
            </div>
            <div class="time">
              <span>离岗时间上限: &nbsp;</span>
              <span>{{ staff.leaveTimeLimit }}分钟</span>
            </div>
          </div>
        </div>
      </div>
      <div class="leave-time-chart-wrapper">
        <LineChart
            :chart-data="chartData"
            :options="options"
            class="leave-time-chart"
        />
      </div>
    </div>
  </div>
</template>

<script>
  import LineChart from "@/components/Charts/LineChart";

  export default {
    name: "WelcomeArea",
    components: {LineChart},
    data() {
      return {
        staffs: [{
          img: 'data:image/png;base64,',
          name: '张三',
          age: '20201215',
          leaveTime: 10,
          leaveTimeLimit: 20,
        }],
        ws: null,
        chartData: {
          labels: Array.from({length: 9}).map((_, i) => 9 + i),
          datasets: [
            {
              label: '离岗时间统计',
              fill: false,
              backgroundColor: '#000',
              borderColor: '#000',
              data: Array.from({length: 9}).map((_, i) => {
                return Math.round( Math.random() * 100);
              })
            }
          ],
        },
        options: {
          responsive: true,
          maintainAspectRatio: false
        }
      }
    },
    mounted() {
      this.openWebSocket();
    },
    beforeDestroy() {
      this.ws?.close(1000, 'closing');
    },
    methods: {
      openWebSocket() {
        let ws = this.ws = new WebSocket("ws://211.67.20.74:8000/warning");
        ws.onopen = () => console.log('open');
        ws.onmessage = (e) => {
          const data = JSON.parse(e.data);
          if (typeof data === 'object') {
            this.infos = [data];
          }
        }
        ws.onclose = () => console.log('close');
        ws.onerror = (error) => console.log(error);
      }
    }
  }
</script>

<style scoped>
  .welcome-area-container {
    color: #dddddd;
    padding: 0.5rem;
    margin: 0 0.5rem 0.5rem 0;
    background: #061123;
    border-radius: 5px;
    height: 50%;
  }

  .welcome-area-container > h2 {
    margin: 0;
  }

  .welcome-area-wrapper {
    display: flex;
    height: calc(100% - 32px);
    justify-content: space-between;
    padding: 10px;
    box-sizing: border-box;
  }

  .staff-wrapper {
    margin: 0.5rem;
  }

  .staff-wrapper .title {
    margin: 0.5rem 0;
  }

  .staff-info {
    display: flex;
    height: 50px;
  }

  .staff-info img {
    height: 50px;
    width: 50px;
    border-radius: 50%;
  }

  .staff-info .info {
    display: flex;
    flex-direction: column;
    height: 50px;
    justify-content: space-around;
    margin-left: 0.5rem;
  }

  .staff .time:nth-child(2) {
    margin: 0.875rem 0;
  }

  .staff .time > span:first-child{
    width: 140px;
    display: inline-block;
    text-align: right;
  }

  .staff .time > span:last-child {
    display: inline-block;
    width: 60px;
    text-align: center;
    background: #4BC0C0;
    padding: 0.3rem;
  }

  .leave-time-chart-wrapper {
    height: 100%;
  }

  .welcome-area-wrapper .leave-time-chart {
    height: 100%;
    background: #dddddd;
  }
</style>